<template>
   <div class="container">
    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" size="small"
        :header-cell-style="{
                    background:'#EBF4FF',
                    color: '#333',
                    fontWeight: 'bold',
                    height: '40px',
                    padding: 0,
                    fontSize: '14px'
                }" class="search-table" height="500" 
        element-loading-text="加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.9)" :cell-style="{color: '#333', fontSize: '14px'}">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column type="index" width="50" label="序号">
        </el-table-column>
        <el-table-column prop="mpiId" label="主索引号" show-overflow-tooltip min-width="220">
        </el-table-column>
        <el-table-column prop="healthInsuranceCardId" label="社保卡号" show-overflow-tooltip min-width="100">
        </el-table-column>
        <el-table-column prop="name" label="患者姓名"  min-width="70" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="sexName" label="性别" min-width="50">
            <template slot-scope="scope">
                <span>{{scope.row.sexName || scope.row.sex }}</span>
            </template>
        </el-table-column>
        <el-table-column prop="birthDate" label="出生日期" min-width="90">
            <template slot-scope="scope">
                <span>{{scope.row.birthDate && scope.row.birthDate.slice(0, 10)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="idCard" label="身份证号" show-overflow-tooltip min-width="120">
        </el-table-column>
        <el-table-column prop="patientPhone" label="联系电话" min-width="100">
        </el-table-column>
        <el-table-column label="操作" class-name="action">
            <template slot-scope="scope">
                <el-tooltip effect="dark" content="详情" placement="bottom">
                    <span class="operate-icon operate-icon1 hand">
                        <i class="iconfont icon-zidianxiang"></i>
                    </span>
                </el-tooltip>
                <el-tooltip effect="dark" content="交叉索引" placement="bottom">
                    <span class="operate-icon hand">
                        <i class="iconfont icon--s-icon_jiaochasuoying"></i>
                    </span>
                </el-tooltip>
                <el-tooltip effect="dark" content="轨迹" placement="bottom">
                    <span class="operate-icon hand">
                        <i class="iconfont icon--s-icon_guiji"></i>
                    </span>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
   </div>
   
</template>
<script>
export default {
    components: {
    },
    data() {
        return {
            searchForm: {
                name: '',
                idCard: '',
                patientPhone: '',
                cardNo: ''
            },
            pageNo: 1,
            pageSize: 10,
            currentPage: 1,
            total: '',
            tableData: [],
        }
    },
    methods: {
        queryMainIndexList() {
            let me = this;
            me.tableLoading = true;
            let params = {
                pageNum: me.pageNo,
                pageSize: me.pageSize,
                isTc: false
            }
            Object.assign(params, me.searchForm)
            $ajax({
                url: 'api/mpiview.mpiManageRpcService/selectPatientInfoByParamsForPage',
                jsonData: [params]
            }).then(function (res) {
                if (res && res.code == 200) {
                   me.tableData = res.body.list;
                   me.pageSize = res.body.pageSize;
                   me.total = res.body.total;
                   me.pageNo = res.body.pageNum;
                }
                me.tableLoading = false;
            }).fail(function (e) {
                console.error(e);
                me.tableLoading = false;
            })
        },
    },
    created() {
        // console.log(me)
        this.queryMainIndexList()
    }
}
</script>
<style lang="scss" scoped>
.container {
    padding: 20px;
}
</style>